.trans-color {
  -webkit-transition: color 0.2s ease-out;
  transition: color 0.2s ease-out;
}

.component {
  position: relative;
}

.toggle {
  white-space: nowrap;
}

.toggle-icon:not(.icon-only) {
  margin-left: 0.5rem;
}

.list {
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: left;
}

.list-main {
  position: absolute;
  top: 100%;
  z-index: 1000;
  visibility: hidden;
  min-width: 160px;
  overflow: auto;
}

.align-left {
  left: 0;
}

.align-right {
  right: 0;
}

.list-item-text,
.list-item-link {
  display: block;
  position: relative;
  white-space: nowrap;
  text-decoration: none;
}

.list-item-text {
  cursor: default;
}

@-webkit-keyframes in {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes in {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@-webkit-keyframes out {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% { opacity: 0; }
}

@keyframes out {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% { opacity: 0; }
}

.close {
  -webkit-animation: out 0.2s ease;
          animation: out 0.2s ease;
  visibility: hidden;
}

.open {
  -webkit-animation: in 0.2s ease;
          animation: in 0.2s ease;
  visibility: visible;
}



